import { LINQ } from 'node-linq';

class Poke {

  canExecute(message, bot) {
    return message.content.indexOf('poke') > -1;
  }

  execute(message, bot) {
    let mentions = new LINQ(message.mentions);
    if (!mentions.Any(e => e.username != 'jk-jarvis')) {
      bot.reply(message, 'Who should i poke?.');
      return;
    }

    var toPoke = mentions.Where(e => e.username != 'jk-jarvis').ToArray();
    for (let i = 0; i < toPoke.length; i++) {
      console.log(toPoke[i]);
      bot.sendMessage(message.channel, '@' + toPoke[i].username + '#' + toPoke[i].discriminator + ' poke!');
    }
  }
}

export default Poke;
